@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-black.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-black.woff2') format('woff2');
	font-display: block;
	font-weight: 900;
}
h1, h2, h3, h4, h5, h6, p, li, td, a, span, button, figure {
	font-family: "Red-Hat-Display";
	font-weight: 300;
}

h1, .title--h1 {
	font-size: 64px;
	line-height: 64px;
	font-weight: 900;
}

h2, .title--h2 {
	font-size: 64px;
	line-height: 64px;
	font-weight: 900;
}

h3, .title--h3 {

}

h4, .title--h4 {
	text-transform: uppercase;
	font-weight: 900;
	letter-spacing: 4px;
	font-size: 16px;;
}

.text {
 font-size: 18px;
 line-height: 26px;
 color: $mantree-grey;
} 

.hero__logo .logo__text {
	line-height: 1;
}

.navigator__index {
	font-size: 16px;
	font-weight: 900;
	color: $mantree-grey;
}
* {
	box-sizing: border-box;
	scrollbar-width: none;
	scroll-behavior: smooth;
}

*::-webkit-scrollbar {
	display: none;
	-webkit-appearance: none;
}

.section {
	overflow: hidden;
}

.button {
	padding: 16px 32px;
	color: white;	
	border-radius: 32px;
	cursor: pointer;

	
	&--version {
		margin-left: 4px;
	}
	
	&--violet {
		background-color: $delunge-violet;

		&:hover { 
			background: lighten($delunge-violet, 10%) 
		}
		
	}
	
	&--blue {
		background-color: $hippie-blue;
		
		&:hover { 
			background: lighten($hippie-blue, 10%) 
		}

	}
	
}


.navigator {
	
	&__line {
		height: 80px;
		width: 1px;
		background-color: transparentize($mantree-grey, 0.75);
		margin: 0 auto;
	}
	
	&__index {
		width: 56px;
		height: 56px;
		border: 1px solid transparentize($mantree-grey, 0.75); 
		border-radius: 50%;
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.hero {
	
	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
	}

	&__logo, .logo {
		display: block;
		margin: 80px auto 104px auto;
		position: relative;
		
		&__point, .point {
			width: 14px;
			height: 14px;
			border-radius: 50%;
			position: absolute;
			
			&--blue {
				background-color: $hippie-blue;
				left: -32px;
				top: 16px;
			}
			
			&--violet {
				background-color: $delunge-violet;
				left: -24px;
			}
			
		}
		
		
		&__text {
			font-size: 24px;
			font-weight: 900;
			text-align: center;
		}
		
	}
	
	&__intro, .intro {
		max-width: 512px;
		margin: 0 auto;
		position: relative;
		
		&__title {
			text-align: center;	
			margin-bottom: 32px;
		}

		&__text {
			text-align: center;
			margin-bottom: 32px;
		}

		&__buttons {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			margin-bottom: 104px;;
		}

		.button {
			font-weight: 900;

			&__version {
				color: $malibu-blue;
			}

			&:not(:last-child) {
				margin-right: 16px;
			}

		}

		
		&__personas, .personas {
			
			&__image {
				width: 110px;
				height: 110px;
				position: absolute; 
				border-radius: 50%;
				top: 1000px;

				&--1 { top: calc(30% - 0px); left: calc(-110px + -80px); }
				&--2 { top: calc(30% - 144px); left: calc(-110px + -80px); }
				&--3 { top: calc(30% - 48px); left: calc(-110px + -240px); }
				&--4 { top: calc(30% - 192px); left: calc(-110px + -240px); }
				&--5 { top: calc(30% - 0px); left: calc(-110px + -400px); }
				&--6 { top: calc(30% - 144px); left: calc(-110px + -400px); }
				&--7 { top: calc(30% - 0px); right: calc(-110px + -80px); }
				&--8 { top: calc(30% - 144px); right: calc(-110px + -80px); }
				&--9 { top: calc(30% - 48px); right: calc(-110px + -240px); }
				&--10 { top: calc(30% - 192px); right: calc(-110px + -240px); }
				&--11 { top: calc(30% - 0px); right: calc(-110px + -400px); }
				&--12 { top: calc(30% - 144px); right: calc(-110px + -400px); }
			}
			
		}

		&__navigator {
			margin-bottom: 64px;
		}
		
	}
	
}
.usecases {

	&__images {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-bottom: 80px;
	}

	&__image {
		width: 100%;
		max-width: 255px;
		max-height: 242px;
		border-radius: 8px;

		&:not(:last-child) {
			margin-right: 32px;
		}

	}

	&__text-container {
		margin: 0 auto;
		max-width: 540px;

	}
	&__pretitle {
		color: $hippie-blue;
		text-align: center;
		margin-bottom: 16px;
	}

	&__title {
		text-align: center;
		margin-bottom: 32px;
	}

	&__text {
		text-align: center;
		margin-bottom: 72px;
	}

}